* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: 'montserrat', sans-serif;
  background-color: #223343;
}
header {
  padding: 50px 15px 0px;
}
header h1 {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  text-align: center;
}
main {
  width: 100%;
  margin: 0 auto;
  max-width: 960px;
}
section {
  padding: 15px;
}
.searchbox {
  display: block;
  width: 100%;
  padding: 8px;
  border: none;
  outline: none;
  background: none;
  background-color: #eee;
  border-radius: 8px;
  color: #53565a;
  font-size: 14px;
  font-weight: 300;
  transition: 0.4s ease-out;
}
.searchbox:focus {
  box-shadow: 0px 0px 8px 3px #4484c4;
}
.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
  justify-content: center;
}
.results .result {
  width: 33.333%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
.results .result img {
  display: block;
  width: 100%;
}
.results .result h3 {
  color: #fff;
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
  background: #445565;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
  text-align: center;
}
.results .result:hover h3 {
  background: #fff;
  color: #223343;
} 
.popup {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: #445565;
  text-align: center;
}
.popup .container {
  max-width: 100%;

}
.popup .container .content {
  padding: 25px;
  color: #fff;
}
.popup .content h2 {
  font-size: 18px;
  font-weight: 600;
}
.popup .content h2 span {
  font-size: 14px;
  font-weight: 300;
}
.popup .content .rating {
  margin-bottom: 30px;
}
.popup .container .content  img {
  max-width: 100%;
  height: auto;
}
.popup .container .content p {
  font-size: 13px;
}
.popup .container .content .plot {
  display: flex;
  justify-content: center;
}
.popup .close {
  display: inline-block;
  padding: 10px 15px;
  font-size: 12px;
  font-weight: 700;
  background: #4484c4;
  color: #fff;
  border-radius: 8px;
  border: none;
  outline: none;
  appearance: none;
  cursor: pointer;
  transition: 0.4s ease-out;
}
.popup .close:hover {
  background: #223343;
}
@media (min-width: 576px) {
  header h1 {
    font-size: 24px;
  }
  .searchbox {
    font-size: 16px;
    padding: 10px;
  }
  .popup .content h2 {
    font-size: 20px;
  }
  .popup .content h2 span {
    font-size: 16px;
  }
  .popup .container .content p {
    font-size: 15px;
  }
}
@media (min-width: 768px) {
  header h1 {
    font-size: 26px;
  }
  .popup .content h2 {
    font-size: 22px;
  }
  .popup .content h2 span {
    font-size: 18px;
  }
  .popup .container .content p {
    font-size: 17px;
  }
  .popup .close {
    font-size: 14px;
  }
}
@media (min-width: 992px) {
  header h1 {
    font-size: 28px;
  }
  .popup .content h2 {
    font-size: 25px;
  }
  .popup .content h2 span {
    font-size: 22px;
  }
  .popup .container .content p {
    font-size: 18px;
  }
  .popup .close {
    font-size: 16px;
  }
}
@media (min-width: 1200px) {
  header h1 {
    font-size: 28px;
  }
  .popup .content h2 {
    font-size: 28px;
  }
  .popup .content h2 span {
    font-size: 24px;
  }
  .popup .close {
    font-size: 18px;
  }
}


